/* eslint-disable react/jsx-no-bind */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Slide from '@material-ui/core/Slide';

import { toast } from 'react-toastify';
import './styles.css';
import _ from 'lodash';
import agent from '../../../agent';
import {
  ADD_TO_CART, REMOVE_FROM_CART,
} from '../../../constants/actionTypes';
import ProductDetails from './ProductDetails';
import CartDetails from './CartDetails';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      product: { },
      quantity: 1,
      images: [],
      alreadyInCart: false,
      cartItem: false,
    };
    this.cartItems = [];
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
    }
    if (this.props.cartItem) {
      this.setState({ cartItem: this.props.cartItem });
    }
    if (this.props.cartItems) {
      console.log(this.props);
      this.cartItems = this.props.cartItems;
      const temp = this.props.cartItems.filter((c) => c.item.id === this.props.data.id);
      console.log(temp);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false });
      }
    }
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  addToCart() {
    const data = {
      item: this.state.product,
      quantity: this.state.quantity,
    };

    this.props.addToCart(data);
  }

  removeProduct() {
    this.props.removeFromCart(this.state.product);
  }

  render() {
    const {
      name, desc, disc_price, price, stock, estimated_delievry, images,
    } = this.state.product;
    const { quantity, alreadyInCart, cartItem } = this.state;
    return (
      <div className="public-product relative">

        <div className="product-header-img relative">
          {
                images.length > 0
                  ? <img src={images[0]} alt="" /> : <img src="" alt="" />
            }
          <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent = {this.props.parent} />
        </div>

        <div className="product-pricing">
          <h3 className="ma0 relative">
            <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent = {this.props.parent} />

            {name}
          </h3>
          {
                disc_price === price
                  ? (
                    <b>
                        Rs.
                      {price}
                    </b>
                  )
                  : (
                    <div>
                      <div className="home-page-product-sale">
                        {Math.floor(((price - disc_price) / price) * 100)}
                        {' '}
%off
                        {' '}
                      </div>
                      <b>
                        Rs.
                        {disc_price}
                      </b>
                      <strike>{` Rs. ${price}`}</strike>

                    </div>
                  )
            }
          {
                cartItem
                  ? (
                    <div className="product-bottom-div">
                      <div className="f6 go-to-cart relative blue" onClick={this.removeProduct.bind(this)}>
                        Remove
                      </div>
                    </div>
                  )
                  : (
                    <div className="product-bottom-div">
                      <div className="flex">
                        <button type="button" className="w-20" onClick={() => this.modifyQuantity(-1)}> - </button>
                        <input type="number" value={quantity} className="w-60" disabled />
                        <button type="button" className="w-20" onClick={() => this.modifyQuantity(1)}>  + </button>
                      </div>
                      <button className="product-add-to-cart" onClick={this.addToCart.bind(this)} disabled={this.props.parent !== 'public-home-page'} > Add to cart </button>
                      {
                alreadyInCart && this.props.parent === 'public-home-page'
                  ? (
                    <div className="f6 go-to-cart relative">
                        Item already in cart.
                      <a>
                      Go To Cart
                      </a>
                      <CartDetails />
                    </div>
                  )
                  : null
            }
                    </div>
                  )

            }

        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,


});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
