import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { Carousel } from 'react-responsive-carousel';
import Slide from '@material-ui/core/Slide';
import { toast } from 'react-toastify';
import CartDetails from './CartDetails';
import './styles.css';
import agent from '../../../agent';
import Loader from '../../../common/Components/Loader';
import DisabledDiv from '../../../common/Components/DisabledDiv';
import TextInput from '../../../common/Components/TextInput';
import SelectImage from '../../../common/Components/SelectImage';
import {
  ADD_TO_CART, REMOVE_FROM_CART,
} from '../../../constants/actionTypes';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      isCreateProductDialog: false,
      alreadyInCart: false,
      user: {},
      product: { images: [] },
      images: [],
      quantity: 1,
    };
    this.cartItems = [];
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
    }
    if (this.props.cartItems && this.props.parent === 'public-home-page') {
      console.log(this.props);
      this.cartItems = this.props.cartItems;
      const temp = this.props.cartItems.filter((c) => c.item.id === this.props.data.id);
      console.log(temp);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data) {
      this.setState({ product: np.data });
    }
    console.log(this.props.parent)
    if (np.cartItems && this.props.parent === 'public-home-page') {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, quantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false });
      }
    }
  }

  togelIsCreateProductDialog() {
    this.setState({ isCreateProductDialog: !this.state.isCreateProductDialog });
  }


  addToCart() {
    const data = {
      item: this.state.product,
      quantity: this.state.quantity,
    };
    console.log(data);
    this.props.addToCart(data);
  }

  removeProduct() {
    this.props.removeFromCart(this.state.product);
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  render() {
    const {
      name, images, price, disc_price, description, id, estimated_delivery,
    } = this.state.product;
    const { alreadyInCart, isCreateProductDialog, quantity } = this.state;
    return (
      <div>

        <button
          className=" center  flex add-new-product "

          onClick={this.togelIsCreateProductDialog.bind(this)}
        >
              see Product details
        </button>

        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted

          fullScreen
          onClose={this.togelIsCreateProductDialog}


        >
          <div className="cart-details-top-bar">
            <h2>{name}</h2>
            <button type="button" onClick={this.togelIsCreateProductDialog.bind(this)}>
                  Close
            </button>
          </div>

          <DialogContent>
            <div className="product-details">


              <div className="product-details-top-div">
                <Carousel className="product-details-carousel">
                  { images.map((img, i) => <img src={img} key={i} />)}
                </Carousel>
                <div className="cart-details-top-right-div">
                  <h1>{name}</h1>
                  <div className="product-pricing">

                    {
                disc_price === price
                  ? (
                    <b>
                        Rs.
                      {price}
                    </b>
                  )
                  : (
                    <div>

                      <b>
                        Rs.
                        {disc_price}
                      </b>
                      <strike>{` Rs. ${price}`}</strike>
                      <span className="product-details-product-sale">
                        {Math.floor(((price - disc_price) / price) * 100)}
%off
                      </span>
                    </div>
                  )
            }
                  </div>

                  <h4>
Esimated Delivery:
                    {estimated_delivery}
                    {' '}
Days
                  </h4>
                  <div className="product-add-to-cart-div">
                    {
                      alreadyInCart
                        ? null
                        : (
                          <div className="flex ">
                            <button type="button" className="w-20" onClick={() => this.modifyQuantity(-1)}> - </button>
                            <input type="number" value={quantity} className="w-60" disabled />
                            <button type="button" className="w-20" onClick={() => this.modifyQuantity(1)}>  + </button>
                          </div>
                        )
                    }

                    {
                      alreadyInCart
                        ? (
                          <button type="button" className="product-add-to-cart relative" >
View Cart
                            <CartDetails />
                            {' '}

                          </button>
                        )
                        : <button type="button" className="product-add-to-cart" onClick={this.addToCart.bind(this)} disabled={this.props.parent !== 'public-home-page'}>Add To Cart </button>
                    }

                    {
                    alreadyInCart
                      ? (

                        <div className="f6 go-to-cart relative">
                        Item already in cart.
                        
                          
                          
                        </div>
                      )
                      : null
                  }
                  </div>
                </div>
              </div>
              <div>
                <h4> Description : </h4>
                <p>{description}</p>
              </div>
            </div>
          </DialogContent>


        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,

});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
